import React from 'react';
import logo from '../assets/images/connectwiseLogo.png'; // with import

export class HeaderHolder extends React.Component {
    render(){
        return(

            <header class="header-area header-sticky background-header">
            <div class="container">
                <div class="row">
                    <div class="col-12">
                        <nav class="main-nav">
                            <a href="#" class="logo">
                                <img  src={logo} style={{width: '35%'}} align="Connectwise Demo App"></img>
                            </a>
                            <ul class="nav">
                                <li class="scroll-to-section"><a href="#top" class="">Home</a></li>
                                <li class="scroll-to-section"><a href="#about" class="">About</a></li>
                               <li class="scroll-to-section"><a href="#reservation" class="">Contact Us</a></li> 
                            </ul>        
                        </nav>
                    </div>
                </div>
            </div>
        </header>

        );
    }
}

export default HeaderHolder;
